import React, { useState, useEffect } from "react";
import logo from "../../assets/images/2gather.jpg";
import AuthController from "../../platform/api/auth";
import {
  isValidEmail,
  isValidPassword
} from "../../platform/services/validator";
import {
  OsTypeEnum,
  LoginProviderTypeEnum
} from "../../platform/constants/enums";
import "./style.scss";

class SignIn extends React.Component {
  state = {
    form: {
      email: "",
      password: ""
    },
    focus: {
      email: false,
      password: false
    },
    passToggle: false,
    inputWarning: false
  };

  submit = async e => {
    e.preventDefault();
    const {
      form: { email, password }
    } = this.state;
    if (isValidEmail(email) && password) {
      try {
        const res = await fetch("https://2.evollai.com/api/auth/login", {
          method: "POST",
          body: JSON.stringify({
            email,
            password,
            osType: "3",
            language: 1
          }),
          headers: {
            "Content-Type": "application/json",
            "Accept-Language": "*"
          }
        });
        const data = await res.json();
        console.log(data);
      } catch (error) {
        console.log(error);
      }
    }
    if ((!email && !isValidEmail(email)) || !password) {
      this.setState({
        inputWarning: true
      });
    }
  };

  change = ({ target: { name, value } }) => {
    console.log(name, value);
    const { form } = this.state;
    form[name] = value;
    console.log(form);
    this.setState({ form });
  };

  onFocus = key => {
    this.setState({
      focus: {
        [key]: true
      }
    });
  };

  onBlur = (e, key) => {
    this.setState({
      focus: {
        [key]: e.target.value !== ""
      }
    });
  };

  togglePassword = () => {
    this.setState({
      passToggle: !this.state.passToggle
    });
  };

  render() {
    const { form, focus, passToggle, inputWarning } = this.state;
    return (
      <div className="sign-in">
        <div className="sign-in-form">
          <div className="sign-in-form-container">
            <h2>Sign in</h2>
            <p>
              Enter your email and password and login in your <br /> account
            </p>
            <form>
              <div className="G-field">
                <label
                  className={`G-form-label  ${
                    focus.email ? "G-label-animated" : ""
                  }`}
                  htmlFor="signin-email"
                >
                  Email
                </label>
                <input
                  type="email"
                  onChange={this.change}
                  name="email"
                  onFocus={() => this.onFocus("email")}
                  onBlur={e => this.onBlur(event, "email")}
                  className={inputWarning ? "G-invalid-input" : ""}
                />

                <span
                  className={`input-border ${
                    focus.email ? "input-border-animated" : ""
                  }`}
                ></span>
              </div>
              <div className="G-field">
                <label
                  className={`G-form-label  ${
                    focus.password ? "G-label-animated" : ""
                  }`}
                  htmlFor="signin-password"
                >
                  Password
                </label>
                <input
                  name="password"
                  type={!passToggle ? "password" : "text"}
                  onChange={this.change}
                  onFocus={() => this.onFocus("password")}
                  onBlur={() => this.onBlur(event, "password")}
                />

                <span
                  className={`input-border ${
                    focus.password ? "input-border-animated" : ""
                  }`}
                ></span>
                <i
                  className={!passToggle ? "icon-ic_eye" : "icon-ic_eyeclosed"}
                  onClick={this.togglePassword}
                ></i>
              </div>

              <button className="G-button" onClick={this.submit}>
                Sign in
              </button>
            </form>
          </div>
        </div>
        <div className="logo">
          <img src={logo} alt="2gather" />
        </div>
      </div>
    );
  }
}

export default SignIn;
